body.show-loader .loader {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

.loader {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  opacity: 0;
  pointer-events: none;
  background-color: rgba(246, 234, 234, .318);
  transition: opacity .25s ease-out;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.no-scroll {
  overflow: hidden;
}

body.show-loader .loader__modal-card {
  transform: translate(-50%, -50%);
}

.loader__modal-card {
  gap: 20px;
  transition: transform .25s ease-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader__dote {
  width: 40px;
  height: 40px;
  background-color: red;
  border-radius: 50%;
  animation: scaling 2.5s ease-in-out infinite;
  display: inline-block;
}

@keyframes scaling {
  0%, 100% {
    background-color: red;
    transform: scale3d(.2, .2, .2);
  }

  50% {
    background-color: #000;
    transform: scale3d(1, 1, 1);
  }
}

.loader__dote:nth-child(0) {
  animation-delay: 0;
}

.loader__dote:nth-child(1) {
  animation-delay: .2s;
}

.loader__dote:nth-child(2) {
  animation-delay: .4s;
}

.loader__dote:nth-child(3) {
  animation-delay: .6s;
}

.loader__dote:nth-child(4) {
  animation-delay: .8s;
}

.loader__dote:nth-child(5) {
  animation-delay: 1s;
}

